<script>
	import Cards from "./Cards.svelte"
	import Title from "./Title.svelte"
	import { onMount } from "svelte"
	import Loading from "./Loading.svelte"
	import SearchBox from "./SearchBox.svelte"
	//const url = "https://gateway.marvel.com:443/v1/public/characters?limit=100&offset=432&ts=thesoer&apikey=ccfdb54fc361f803d07855f3895d14b0&hash=8985759a11dab023d6f13a17d1fba148"
	const url = "/characters.json"
	let data, characters = null

	onMount(async () => {
		const results = await fetch(url)
		data = await results.json()
		characters = data
	})
</script>

<style>
	@import url('https://fonts.googleapis.com/css?family=B612&display=swap');
	.content {
		width: 70%;
		margin: auto;
		background-color: #2c3e50;
	}

	.cards {
		background-color: rgb(189, 195, 199);
		border-radius: 25px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
		padding-bottom: 4%;
	}
	</style>

<div class="content">
	<Title />
	<SearchBox data={data} bind:characters={characters} />
	{#if data == null}
	<Loading />
	{:else}
	<div class="cards">
	<Cards characters={characters}/>
	</div>
	{/if}
</div>
